
<template>
  <form action="/api/standards/standard" method="post" class="fon" @submit.prevent="submitForm" >
    <input name="title" id="title" placeholder="title" v-model="standard.title">
    <input name="designation" placeholder="designation" v-model="standard.designation">
    <input name="cipher" placeholder="cipher" v-model="standard.cipher">
    <input type="submit" class="btn" value="Добавить">
  </form>
</template>
<script>

import {AXIOS} from "../../http-common";

export default {
  name: 'FormForAddExspertise',
  data(){
    return{
      standard:{
        title:'',
        designation:'',
        cipher:''
      },

    }
  },
  methods:{

      submitForm() {
        AXIOS.post('/standards/standard', this.standard)
            .then(response => {
              console.log(response.data);
              // дальнейшая обработка ответа
            })
            .catch(error => {
              console.error(error);
              // обработка ошибок
            });
      }
    }




}
</script>

<style scoped lang="scss">
.fon{
  background-color: aliceblue;
}
</style>